<template>
  <div class="loginview">
    <div class="bg">
      <img src="../assets/img/t6.png" alt="" />
    </div>
    <div class="logintop">
      <span class="iconfont icon-xiangzuojiantou" @click="goPrev"></span>
      <router-link tag="p" to="/registerview">注册</router-link>
    </div>
    <h1>登录</h1>
    <div class="loginInfo">
      <div class="area">
        <p>中国大陆 +86</p>
        <span class="iconfont icon-jinrujiantou"></span>
      </div>
      <input type="text" placeholder="请输入手机号" v-model="phone" />
      <input type="password" placeholder="请输入密码" v-model="pwd" />
    </div>
    <div class="box">
      <div class="canflex"></div>
      <div class="forbidloginbtn" v-show="!isPass">登 录</div>
      <div class="loginbtn" v-show="isPass" @click="userLogin">登 录</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      phone: "",
      pwd: "",
    };
  },
  methods: {
    userLogin() {
      let userMsg = localStorage.getItem("userlist");
      let _this = this;
      if (!userMsg) {
        this.$toast.fail("账号或密码错误");
        return;
      }
      let userlist = JSON.parse(userMsg);
      let index = userlist.findIndex((item) => item.phone == this.phone);
      if (index == -1) {
        this.$toast.fail("账号或密码错误");
        return;
      } else {
        if (this.pwd != userlist[index].pwd) {
          this.$toast.fail("账号或密码错误");
          return;
        }
      }
      userlist[index].type = 'login';
      userlist = JSON.stringify(userlist)
      localStorage.setItem("userlist",userlist);
      this.$toast.success({
        message: "登录成功",
        duration: 2000,
        onClose(){
          _this.$router.push("/myview")
        }
      });
    },
    goPrev(){
      this.$router.go(-1)
    }
  },
  computed: {
    isPass() {
      if (this.phone != "" && this.pwd != "") {
        return true;
      }
      return false;
    },
  },
};
</script>

<style lang="less">
.loginview {
  width: 100%;
  height: 100%;
  .bg {
    width: 100%;
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: -1;
    img {
      width: 100%;
    }
  }
  .logintop {
    //   width: 100%;
    height: 40px;
    line-height: 40px;
    padding-left: 12px;
    padding-right: 16px;
    display: flex;
    justify-content: space-between;
    .iconfont {
      font-weight: bold;
    }
    p {
      font-size: 15px;
    }
  }
  h1 {
    font-size: 28px;
    font-weight: bold;
    font-family: "黑体";
    margin-top: 28px;
    margin-left: 20px;
    line-height: 32px;
  }
  .loginInfo {
    margin: 0 20px;
    margin-top: 44px;
    .area {
      display: flex;
      justify-content: space-between;
      height: 58px;
      line-height: 58px;
      font-size: 15px;
      border-bottom: 1px solid #f5f5f5;
    }
    input {
      width: 100%;
      height: 58px;
      line-height: 58px;
      background: none;
      outline: none;
      border: none;
      border-bottom: 1px solid #f5f5f5;
      font-size: 15px;
    }
  }
  .box {
    display: flex;
    justify-content: space-between;
    margin-top: 28px;
    .forbidloginbtn {
      width: 92px;
      height: 38px;
      line-height: 38px;
      margin-right: 20px;
      text-align: center;
      border-radius: 19px;
      background-color: #e5e5e5;
      font-size: 14px;
      color: #fff;
      font-weight: bold;
    }
    .loginbtn {
      width: 92px;
      height: 38px;
      line-height: 38px;
      margin-right: 20px;
      text-align: center;
      border-radius: 19px;
      background-color: #e45e51;
      font-size: 14px;
      color: #fff;
      font-weight: bold;
    }
  }
}
</style>